#client-info,
#logistics-info,
#ship-info,
#wait-info,
#detail-info {
  scroll-margin-top: 60px;
  /* 設置滾動計算時頂部外部填充為header高度60px */
}

.choicedate {
  border: 1px solid rgba(1, 41, 112, 0.3);
  padding: 7px;
  border-radius: 3px;
  font-size: 14px;
  margin-left: 10px;
  min-width: 110px;

  &:hover,
  &:focus &:active {
    outline: none;
    box-shadow: 0 0 10px 0 rgba(1, 41, 112, 0.15);
    border: 1px solid rgba(1, 41, 112, 0.3);
  }
}

.choicedate_span {
  margin-left: 10px;
}

.pdabutn {
  background-color: black;
  color: #fff;
  border-radius: 8px;
  border: none;
  padding: 4px;
}

.tip {
  font-size: 14px;
  color: #851113;
  margin-left: 16px;
  font-weight: 500;
}

.table-2 {
  table {
    th {
      &:last-child {
        width: auto;
      }
    }
  }
}

#logistics-info {
  table {
    thead {
      tr {
        th {
          &:nth-child(7) {
            width: 25%;
          }
          &:last-child {
            width: auto;
          }
        }
      }
    }
    tbody {
      tr {
        td {
          &:nth-child(7) {
            text-align: left;
            width: 25%;
          }
          &:last-child {
            width: auto;
          }
        }
      }
    }
  }
}
#ship-info {
  table {
    thead {
      tr {
        th {
          &:nth-child(8) {
            width: 25%;
          }
          &:last-child {
            width: auto;
          }
        }
      }
    }
    tbody {
      tr {
        td {
          &:nth-child(8) {
            text-align: left;
            width: 25%;
          }
          &:last-child {
            width: auto;
          }
        }
      }
    }
  }
}
#wait-info {
  table {
    thead {
      tr {
        th {
          &:nth-child(7) {
            width: 25%;
          }
        }
      }
    }
    tbody {
      tr {
        td {
          &:nth-child(7) {
            text-align: left;
            width: 25%;
          }
        }
      }
    }
  }
}

.sort-down-butn {
  background: no-repeat;
  border: none;
  color: #fff;
}

.link {
  background-color: #3c4b64;
  color: #fff;
  padding: 8px 10px;
  border-radius: 4px;
  border: none;
  font-size: 16px;
  margin-left: 16px;
}

.card-img-overlay {
  background-color: rgba(255, 255, 255, 0.6);
}

.datecheck {
  position: relative;
}

.datecheck::-webkit-calendar-picker-indicator {
  position: absolute;
  right: 0;
  padding-left: 100%;
}

.search-butn {
  border: 0;
  padding: 0;
  margin-left: -30px;
  background: none;
  font-size: 14px;
}

.detail-box {
  border: none;
  background: #fff;
  font-size: 25px;
}

.dialog-box {
  .dialog-list {
    display: flex;
    padding: 10px 0;
    &:nth-child(2n) {
      background-color: #fbfbfb;
    }
    .box-title {
      font-weight: 600;
      padding-left: 16px;
    }
  }
}

.customAlert {
  align-items: center;
  text-align: center;
  font-weight: bold;
  button {
    width: 20%;
    margin-bottom: 15px;
  }
}

.sortColor {
  color: #feda00;
}

button:disabled {
  cursor: not-allowed;
}

.waitingSelect {
  font-size: 14px;
  margin-left: 10px;
  padding: 7px 0;
}

.logisticDetailTable {
  thead tr th {
    &:nth-child(3) {
      width: 30%;
    }
  }
}

.bi-exclamation {
  &::before {
    background: black;
    color: white;
    border-radius: 50%;
    margin-left: 5px;
  }
}

a.exclamation {
  text-decoration: none;
}
a.exclamation:hover {
  position: relative;
}
a.exclamation span {
  display: none;
}
a.exclamation:hover span {
  background-color: white;
  color: black;
  border: black 1px solid;
  padding: 5px 20px 5px 5px;
  display: block;
  z-index: 100;
  left: 0px;
  margin: 10px;
  width: 400px;
  position: absolute;
  top: 10px;
  font-size: 14px;
  text-decoration: none;
}

.customerCodeInput {
  min-width: 400px;
}
